import React, {useState, useEffect} from 'react';
import './app.css';
import Peg from './peg';

function App() {
  const [pegSize, setPegSize] = useState(25);
  const [pegRad, setPegRad] = useState(50);
  const [numRows, setNumRows] = useState(0);
  const [numCols, setNumCols] = useState(0);
  let grid = [];
  useEffect( () => {setNumRows(()=>Math.floor((window.innerHeight*.95)/25))
    setNumCols(()=>Math.floor((window.innerWidth*.99)/25))},[])

  function buildGrid() {
    for (let i=0; i<numRows; i++){
      let row = [];
      for (let k=0; k<numCols; k++){
        row.push(`${i},${k}`);
      }
      grid.push(row);
    }
  }

  return (
    <div className="App">
      <form onChange={buildGrid()} id="options" >
        <label>peg size<input type="number" value={pegSize} onChange={(e) => {setPegSize(e.target.value)}} min="2"/></label>
        <label>peg spacing<input type="range" value={pegRad} onChange={(e) => {setPegRad(e.target.value)}} min="0" max="71"/></label>
        <label>rows<input type="number" value={numRows} onChange={(e) => {setNumRows(e.target.value)}} min="1"/></label>
        <label>columns<input type="number" value={numCols} onChange={(e) => {setNumCols(e.target.value)}} min="1"/></label>
      </form>
      <main id="grid" style={{
        gridTemplateColumns: `repeat(${numCols}, ${pegSize}px)`,
        width: `${numCols * pegSize}`
        }}>
        {grid.map((row)=> row.map((col)=> <Peg rad={pegRad} key={col} size={pegSize}/>))}
      </main>
    </div>
  );
}

export default App;
